.header {
  max-width: 880px;
  width: 100%;
  margin: 0 auto;
  border-bottom: 1px solid rgba(84, 84, 84, .7);
  padding-top: 45px;
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 950px) {
  .header {
    max-width: 320px;
    padding-top: 28px;
  }
}
