.profile__info {
  margin: auto 0 0 0;
  padding: 0;
  font-size: 18px;
  line-height: 1.2;
  margin-top: 9px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 950px) {
  .profile__info {
    margin-top: 8px;
    font-size: 14px;
  }
}
